"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useLanguage } from "@/components/LanguageProvider";
import { postsAPI } from "@/lib/postsApi";
import { requireAdminAuth } from "@/lib/adminAuth";

export default function CreatePostPage() {
  const router = useRouter();
  const { t } = useLanguage();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [categories, setCategories] = useState([]);
  const [formData, setFormData] = useState({
    CategoryId: "",
    Translations: [
      {
        LanguageCode: 2, // English by default
        Title: "",
        Content: "",
      },
    ],
  });

  useEffect(() => {
    if (!requireAdminAuth(router)) {
      return;
    }
    // TODO: Load categories
    loadCategories();
  }, [router]);

  const loadCategories = async () => {
    // TODO: Implement categories API
    setCategories([
      { Id: "1", Name: "Institute News" },
      { Id: "2", Name: "Events" },
      { Id: "3", Name: "Announcements" },
    ]);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleTranslationChange = (index, field, value) => {
    setFormData((prev) => ({
      ...prev,
      Translations: prev.Translations.map((trans, i) =>
        i === index ? { ...trans, [field]: value } : trans
      ),
    }));
  };

  const addTranslation = () => {
    setFormData((prev) => ({
      ...prev,
      Translations: [
        ...prev.Translations,
        {
          LanguageCode: 2,
          Title: "",
          Content: "",
        },
      ],
    }));
  };

  const removeTranslation = (index) => {
    if (formData.Translations.length > 1) {
      setFormData((prev) => ({
        ...prev,
        Translations: prev.Translations.filter((_, i) => i !== index),
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      const response = await postsAPI.createPost(formData);
      if (response.Result) {
        router.push("/admin/posts");
      } else {
        setError(response.Message || "Failed to create post");
      }
    } catch (error) {
      console.error("Error creating post:", error);
      setError("Failed to create post");
    } finally {
      setLoading(false);
    }
  };

  const languages = [
    { code: 1, name: "Arabic" },
    { code: 2, name: "English" },
    { code: 3, name: "French" },
    { code: 4, name: "German" },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center">
              <button
                onClick={() => router.push("/admin/posts")}
                className="mr-4 text-gray-600 hover:text-gray-900"
              >
                ‚Üê Back to Posts
              </button>
              <h1 className="text-2xl font-bold text-gray-900">
                Create New Post
              </h1>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-4xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-6 sm:px-0">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Error Message */}
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-md">
                {error}
              </div>
            )}

            {/* Category Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Category *
              </label>
              <select
                name="CategoryId"
                value={formData.CategoryId}
                onChange={handleInputChange}
                required
                className="admin-input"
              >
                <option value="">Select a category</option>
                {categories.map((category) => (
                  <option key={category.Id} value={category.Id}>
                    {category.Name}
                  </option>
                ))}
              </select>
            </div>

            {/* Translations */}
            <div>
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-medium text-gray-900">
                  Post Content
                </h3>
                <button
                  type="button"
                  onClick={addTranslation}
                  className="admin-button admin-button-secondary"
                >
                  Add Language
                </button>
              </div>

              {formData.Translations.map((translation, index) => (
                <div key={index} className="admin-card p-6 mb-4">
                  <div className="flex justify-between items-center mb-4">
                    <h4 className="text-md font-medium text-gray-900">
                      Translation {index + 1}
                    </h4>
                    {formData.Translations.length > 1 && (
                      <button
                        type="button"
                        onClick={() => removeTranslation(index)}
                        className="text-red-600 hover:text-red-900"
                      >
                        Remove
                      </button>
                    )}
                  </div>

                  <div className="grid grid-cols-1 gap-4">
                    {/* Language Selection */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Language *
                      </label>
                      <select
                        value={translation.LanguageCode}
                        onChange={(e) =>
                          handleTranslationChange(
                            index,
                            "LanguageCode",
                            parseInt(e.target.value)
                          )
                        }
                        required
                        className="admin-input"
                      >
                        {languages.map((lang) => (
                          <option key={lang.code} value={lang.code}>
                            {lang.name}
                          </option>
                        ))}
                      </select>
                    </div>

                    {/* Title */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Title *
                      </label>
                      <input
                        type="text"
                        value={translation.Title}
                        onChange={(e) =>
                          handleTranslationChange(
                            index,
                            "Title",
                            e.target.value
                          )
                        }
                        required
                        className="admin-input"
                        placeholder="Enter post title"
                      />
                    </div>

                    {/* Content */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Content *
                      </label>
                      <textarea
                        value={translation.Content}
                        onChange={(e) =>
                          handleTranslationChange(
                            index,
                            "Content",
                            e.target.value
                          )
                        }
                        required
                        rows={8}
                        className="admin-input"
                        placeholder="Enter post content (HTML supported)"
                      />
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Submit Buttons */}
            <div className="flex justify-end space-x-4">
              <button
                type="button"
                onClick={() => router.push("/admin/posts")}
                className="admin-button admin-button-secondary"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={loading}
                className="admin-button admin-button-primary"
              >
                {loading ? "Creating..." : "Create Post"}
              </button>
            </div>
          </form>
        </div>
      </main>
    </div>
  );
}
